















































































import Vue from "vue";
export default Vue.extend({
  name: "QImgZoom",
  data() {
    return {
      pageX: 0,
      pageY: 0,
      bigImgWidth: 0,
      bigImgHeight: 0,
      bigImgLeft: 0,
      bigImgTop: 0,
      show: false,
      imgWidth: 0,
      isClick: false,
      rotateDeg: 0,
      margin_left: 0,
      margin_top: 0,
      divHeight: 0,
      divWidth: 0,
      backSizeX: 0,
      backSizeY: 0,
      mouseX: 0,
      mouseY: 0
    }
  },
  props: {
    src: {
      type: String,
      default: 'https://cdn.softtech.com.tr/ngsp-quick/nemo/dev/mdImages/QImgZoom/defaultPhoto.jpg'
    },
    zoomedAreaHeight: {
      type: Number,
      default: 100
    },
    zoomedAreaWidth: {
      type: Number,
      default: 100
    },
    zoomedAreaOpacity: {
      type: String,
      default: '0.2'
    },
    zoomedAreaColor: {
      type: String,
      default: '#000'
    },
    zoomedImageHeight: {
      type: Number,
      default: 500
    },
    zoomedImageWidth: {
      type: Number,
      default: 500
    },
    marginLeft: {
      type: Number,
      default: 5
    },
    height: {
      type: Number,
      default: 400
    },
    width: {
      type: Number,
      default: 400
    },
    zoomType: {
      type: String,
      default: 'NextTo'
    },
    zoomRate: {
      type: Number,
      default: 2
    },
    imageFit: {
      type: String,
      default: 'cover'
    },
    disableZoom: {
      type: Boolean,
      default: false
    },
    hideRotation: {
      type: Boolean,
      default: false
    },
    zoomInUrl: {
      type: String
    },
    zoomOutUrl: {
      type: String
    }
  },
  mounted() {
    this.divHeight = this.height;
    this.divWidth = this.width;
  },
  methods: {
    mouseMove(event: any) {
      if (this.disableZoom) {
        return;
      }
      if (!this.show) {
        this.show = true;
      }
      let imgShow = (<any>this.$refs.imgShow).getBoundingClientRect();
      this.mouseX = event.clientX - imgShow.left;
      this.mouseY = event.clientY - imgShow.top;
      switch (this.zoomType) {
        case 'On':
          this.zoomTypeOn(this.mouseX, this.mouseY);
          break;
        case 'NextTo':
          this.zoomTypeNextTo(this.mouseX, this.mouseY);
          break;
      }

    },
    mouseLeave() {
      this.show = false;
      this.isClick = false;
    },
    imageClick() {
      if (this.zoomType === 'On') {
        this.isClick = !this.isClick;
      }
    },
    rotate(angle?: 0 | 90 | 180 | 270 | 360 | -90 | -180 | -270 | -360) {
      this.rotateDeg += angle ?? 90;
      if (this.rotateDeg % 180 !== 0 && this.rotateDeg % 90 === 0) {
        this.margin_top = (this.width - this.height) / 2;
        this.margin_left = (this.height - this.width) / 2;
        this.divWidth = this.height;
        this.divHeight = this.width;
      } else {
        this.margin_left = 0;
        this.margin_top = 0;
        this.divWidth = this.width;
        this.divHeight = this.height;
      }
    },
    zoomTypeOn(x: number, y: number) {
      if (this.isClick) {
        let zoomRateForTypeOn = this.zoomRate < 2 ? this.zoomRate : 2;
        this.backSizeX = this.width * zoomRateForTypeOn;
        this.backSizeY = this.height * zoomRateForTypeOn;

        if (this.rotateDeg % 360 === 0) {
          this.pageX = x / this.width * 100;
          this.pageY = y / this.height * 100;
        }
        else if ((this.rotateDeg > 0 && this.rotateDeg % 270 === 0) || (this.rotateDeg < 0 && this.rotateDeg % 270 !== 0 && this.rotateDeg % 180 !== 0 && this.rotateDeg % 90 === 0)) {
          this.pageX = 100 - (y / this.width * 100);
          this.pageY = x / this.height * 100;
        }
        else if (this.rotateDeg % 180 === 0) {
          this.pageX = 100 - (x / this.width * 100);
          this.pageY = 100 - (y / this.height * 100);
        }
        else if (this.rotateDeg % 90 === 0 || (this.rotateDeg < 0 && this.rotateDeg % 270 === 0)) {
          this.pageX = y / this.width * 100;
          this.pageY = 100 - (x / this.height * 100);
        }
      }

    },
    zoomTypeNextTo(x: number, y: number) {
      var imageW = (<any>this.$refs.imgShow).clientWidth;
      var imageH = (<any>this.$refs.imgShow).clientHeight;
      this.imgWidth = imageW + this.marginLeft;
      var wTimes = this.zoomedImageWidth / this.zoomedAreaWidth;
      var hTimes = this.zoomedImageHeight / this.zoomedAreaHeight;
      this.bigImgWidth = wTimes * imageW;
      this.bigImgHeight = hTimes * imageH;
      if (x <= this.zoomedAreaWidth / 2) {
        x = 0;
      } else if (x >= imageW - this.zoomedAreaWidth / 2) {
        x = imageW - this.zoomedAreaWidth;
      } else {
        x = x - this.zoomedAreaWidth / 2;
      }
      if (y < this.zoomedAreaHeight / 2) {
        y = 0;
      } else if (y >= imageH - this.zoomedAreaHeight / 2) {
        y = imageH - this.zoomedAreaHeight;
      } else {
        y = y - this.zoomedAreaHeight / 2;
      }
      this.pageX = x;
      this.pageY = y;
      this.bigImgLeft = -x * wTimes;
      this.bigImgTop = -y * hTimes;
      if (this.rotateDeg % 180 !== 0 && this.rotateDeg % 90 === 0) {
        if (this.height > this.width) {
          this.bigImgLeft += this.zoomedImageWidth;
          this.bigImgTop -= this.zoomedImageHeight;
        }
        else if (this.height < this.width) {
          this.bigImgLeft -= this.zoomedImageWidth;
          this.bigImgTop += this.zoomedImageHeight;
        }
        let width = this.bigImgWidth;
        this.bigImgWidth = this.bigImgHeight;
        this.bigImgHeight = width;
      }
    }
  },
  computed: {
    imageStyle() {
      let style: any = {
        transform: `rotate(${this.rotateDeg}deg)`,
        marginLeft: `${this.margin_left}px`,
        marginTop: `${this.margin_top}px`,
        objectFit: this.imageFit,
        width: `${this.width}px`,
        height: `${this.height}px`
      }
      if (this.zoomType === 'On') {
        if (this.show && this.isClick) {
          style = {
            ...style,
            width: this.bigImgWidth ? `${this.bigImgWidth}px` : `${this.width}px`,
            height: this.bigImgHeight ? `${this.bigImgHeight}px` : `${this.height}px`,
            left: `${this.bigImgLeft}px`,
            top: `${this.bigImgTop}px`,
            opacity: 0,
          }
        }
      }
      return style;
    },
    divStyle() {
      let style: any = {
        objectFit: this.imageFit,
        width: `${this.divWidth}px`,
        height: `${this.divHeight}px`
      };
      if (!this.disableZoom && this.zoomType === 'On') {
        if (this.isClick) {
          style = {
            ...style,
            cursor: `url(${this.zoomOutUrl}) 15 15, zoom-out`,
            backgroundImage: `url(${this.src})`,
            backgroundPosition: `${this.pageX}% ${this.pageY}%`,
            width: `${this.width}px`,
            height: `${this.height}px`,
            transform: `rotate(${this.rotateDeg}deg)`,
            marginLeft: `${this.margin_left}px`,
            marginTop: `${this.margin_top}px`,
            backgroundSize: `${this.backSizeX}px ${this.backSizeY}px`
          }
        } else {
          style = {
            ...style,
            cursor: `url(${this.zoomInUrl}) 15 15, zoom-in`
          }
        }
      }
      return style;
    }
  },
  watch: {
    isClick() {
      this.zoomTypeOn(this.mouseX, this.mouseY);
    }
  }

});
